.card-text {
  font-size: 22px;
  font-family: Arimo, sans-serif;
  padding-right: 15px;
  line-height: 1.4em;
  margin-top: -7px;
  text-align: right;
  width: 70%;
  padding-bottom: 30px;
}

.card-container {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 7px;
}

@media only screen and (max-width: 520px) {
  .card-text {
    width: 98%;
  }
}
